	import shopCartDetails from './shopCartDetails.vue';
import { mapGetters, mapActions } from 'vuex';

export default {
	data() {
		return {
			goodsList: [],
			fold: true // 购物车详情页折叠状态,true表示折叠
		};
	},
	props: {
		shopInfo: {
			type: Object,
			default: () => {}
		}
	},
	watch: {
		watchOption() {
			this.getGoodsList();
		}
	},
	created() {
		this.getGoodsList();
	},
	computed: {
		...mapGetters(['watchOption']),
		totalPrice() {
			let total = 0;
			this.goodsList.forEach(item => {
				total += item.food_price * item.food_count;
			});
			return total;
		},
		totalCount() {
			let count = 0;
			this.goodsList.forEach(item => {
				count += item.food_count;
			});
			return count;
		},
		minPrice() {
			if (this.shopInfo) {
				return this.shopInfo.float_minimum_order_amount;
			}
		},
		payClass() {
			if (this.totalPrice < this.minPrice) {
				return 'not-enough';
			} else {
				return 'enough';
			}
		},
		payDesc() {
			if (this.totalPrice === 0) {
				return `¥${this.minPrice}元起送`;
			} else if (this.totalPrice < this.minPrice) {
				let diff = this.minPrice - this.totalPrice;
				return `还差${diff}`;
			} else {
				return '去结算';
			}
		}
	},
	components:{
		shopCartDetails
	},
	methods: {
		getGoodsList() {
			let that = this
			uni.getStorage({
				key: 'goodsList',
				success: res => {
					that.goodsList = res.data;
					// console.log(this.goodsList);
				},
				fail() {
					that.goodsList = []
				}
			});
		},
		toggleList() {
			if (!this.totalCount) {
				return;
			}
			this.fold = !this.fold;
		},
		changeFold() {
			this.fold = true;
		},
		...mapActions([
		      'setOrderInfo'
		    ]),
		pay() {
			if (this.totalPrice < this.minPrice) {
				return;
			}
			this.setOrderInfo({
        shopInfo: this.shopInfo,
        selectFoods: this.goodsList
      })
			uni.navigateTo({
				url:'../orders/orders'
			})
		}
	}
};
